/* Projects Page Container */
.projects-page-container {
    max-width: 1400px;
    margin: 0 auto;
    padding: 1rem;
  }
  
  /* Contact Header (used at the top of the page) */
  .contact-header {
    text-align: center;
    margin-bottom: 2rem;
  }
  
  .small-heading {
    font-size: 1rem;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: #253635;
    margin-bottom: 0.5rem;
  }
  
  .main-heading {
    font-size: 2rem;
    margin: 0.5rem 0;
    color: #333;
  }
  
  .sub-text {
    font-size: 1rem;
    line-height: 1.5;
    color: #666;
    max-width: 800px;
    margin: 0.5rem auto;
  }
  
  /* Contact Form Container (if used on projects page) */
  .contact-container {
    display: flex;
    flex-direction: column;
    max-width: 600px;
    margin: 2rem auto;
    gap: 1rem;
  }
  
  .contact-left {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .form-group {
    display: flex;
    flex-direction: column;
  }
  
  .error-message {
    color: red;
    font-size: 0.85rem;
    margin-top: 0.2rem;
  }
  
  .submit-button {
    align-self: flex-start;
    padding: 10px 20px;
    background-color: #304442;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .contact-submitted-message {
    text-align: center;
    margin: 2rem auto;
  }
  
  /* Projects Grid Styles */
  .projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
    margin-top: 1rem;
  }
  
  /* Project Card Styles */
  .project-card {
    display: flex;
    flex-direction: column;
    background: #fff;
    color: #333;
    text-decoration: none;
    border-radius: 8px;
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .project-card:hover {
    transform: translateY(-4px);
    box-shadow: 0 6px 14px rgba(0, 0, 0, 0.12);
  }
  
  /* Project Image */
  .project-image {
    width: 100%;
    height: 150px;
    background-size: cover;
    background-position: center;
  }
  
  /* Project Info */
  .project-info {
    padding: 10px;
  }
  
  .project-title {
    font-size: 1.2rem;
    font-weight: 700;
    margin: 0 0 0.5rem;
  }
  
  .project-short {
    font-size: 0.95rem;
    line-height: 1.4;
    color: #555;
    margin: 0;
  }
  